<template>
  <span class="output">$${{latex}}$$</span>

</template>

<script>
import { ref, onMounted, watch } from "vue";

export default {
  name: 'TheLate2Math',
  props:{
    latex:{
      type:String,
      required:true
    }
  },
  setup(props) {
    const myFormula = ref("$$"+props.latex+"$$") //Ëá™Â∑±ÊåáÂÆöÂÖ¨ÂºèÔºå‰πüÂèØ‰ª•Âä®ÊÄÅËæìÂÖ•
    
    //Ê∏≤ÊüìÂáΩÊï∞ÔºåË∞ÉÁî®Êó∂‰ºöÊ∏≤ÊüìÊåáÂÆöËäÇÁÇπelementsÔºåÂ¶ÇÊûúÊ≤°ÊúâÊåáÂÆöËäÇÁÇπÔºåÊ∏≤ÊüìÈ°µÈù¢‰∏äÊâÄÊúâÂÖ¨Âºè
    //elementsÂèØ‰ª•ÊòØ‰∏Ä‰∏™DOMËäÇÁÇπÁöÑÊï∞ÁªÑ(Ê≥®ÊÑègetXXXsByYYYÁöÑÁªìÊûúÊòØcollectionÔºåÂøÖÈ°ªÊâãÂä®ËΩ¨‰∏∫Êï∞ÁªÑÊâçË°å)
    const TypeSet = async function (elements) {
      console.log(window.MathJax,"//window.MathJax")
      // console.log(elements)
      if (!window.MathJax) {
        return
      }
      // console.log(window.MathJax)
      window.MathJax.startup.promise = window.MathJax.startup.promise
      .then(() => {
        return window.MathJax.typesetPromise(elements)
      })
      .catch((err) => console.log('Typeset failed: ' + err.message))
      
      return window.MathJax.startup.promise
    }
    
    //onMountedÁöÑÊó∂ÂÄôÂøÖÈ°ªË∞ÉÁî®
    onMounted(()=>{
      TypeSet()
      //ËøôÈáåÂÜôËá™Â∑±ÁöÑonMountedÂÜÖÂÆπ
    })
    
    watch(()=> props.latex,
    (newValue)=>{
      //Áõ¥Êé•Ë∞ÉÁî®TypeSet()‰πüÂèØ‰ª•Ôºå‰ΩÜÊòØ‰ºöÈáçÊñ∞Ê∏≤ÊüìÈ°µÈù¢ÊâÄÊúâÂÖ¨Âºè
      TypeSet([document.getElementsByClassName("output")])
    })
    
    return {
      myFormula,
    }
  }
}

</script>
